<template>
  <div class="app">
    <router-view />
    <div class="snack_contents">
      <v-snackbar
        v-model="snackbar"
        :multi-line="multiLine"
        absolute
        auto-height
        :timeout="timeout"
      >
        {{ text }}
      </v-snackbar>
    </div>
  </div>
</template>

<script>
import * as CONFIG from "./utils/config";
import { EventBus, EVENT_SHOW_SNACKBAR } from "./utils/events-bus";

export default {
  data: () => ({
    multiLine: false,
    snackbar: false,
    text: "",
    timeout: 3000
  }),
  created() {
    CONFIG.initialize();
    EventBus.$on(EVENT_SHOW_SNACKBAR, msg => {
      this.snackbar = true;
      this.text = msg;
    });
  },
  methods: {
    showError() {
      this.snackbar = true;
    }
  }
};
</script>

<style>
* {
  font-family: "Nanum Gothic", "Roboto";
}

.app {
  background-color: black;
}

</style>
