<template>
  <div ref="bg" class="root">
    <div class="title">User Name</div>
    <form class="horizontal_line">
      <input
        style="flex: 7"
        class="edit"
        type="text"
        v-model="user_name"
        @keyup.enter="applyName"
      />
      <div style="flex: 1" class="divider">:</div>
      <v-btn style="flex: 2" @click="applyName">APPLY</v-btn>
    </form>

    <div class="title">Web RTC</div>

    <div class="horizontal_line">
      <div class="left_option">Signaling URL</div>
      <div style="flex: 0.1" class="divider">:</div>
      <input
        class="edit right_option"
        type="text"
        v-model="web_signaliing_url"
      />
    </div>

    <div class="title" style="flex: 1">Receiver</div>

    <div class="horizontal_line">
      <div class="left_option">Stream Name</div>
      <div style="flex: 0.1" class="divider">:</div>
      <input class="edit right_option" type="text" v-model="receive_stream" />
    </div>

    <div class="horizontal_line">
      <v-btn style="flex: 1; margin-top: 20px" @click="showVideo">PLAY</v-btn>
    </div>

    <div class="title" style="flex: 1">Sender</div>

    <div class="horizontal_line">
      <div class="left_option">Stream Name</div>
      <div style="flex: 0.1" class="divider">:</div>
      <input class="edit right_option" type="text" v-model="sender_stream" />
    </div>

    <div class="horizontal_line">
      <div class="left_option">Video Bitrate</div>
      <div style="flex: 0.1" class="divider">:</div>
      <input class="edit right_option" type="text" v-model="video_bitrate" />
    </div>

    <div class="horizontal_line">
      <div class="left_option">Video Source</div>
      <div style="flex: 0.1" class="divider">:</div>
      <select
        id="camera-list-select"
        class="edit right_option"
        ref="cam_select"
        type="text"
        @change="changeCam"
      >
        <option
          v-for="item in video_list"
          :key="item.index"
          :value="item.deviceId"
          >{{ item.label }}</option
        >
      </select>
    </div>

    <div class="horizontal_line">
      <div class="left_option">Audio Bitrate</div>
      <div style="flex: 0.1" class="divider">:</div>
      <input class="edit right_option" type="text" v-model="audio_bitrate" />
    </div>

    <div class="horizontal_line">
      <div class="left_option">Audio Source</div>
      <div style="flex: 0.1" class="divider">:</div>
      <select
        id="mic-list-select"
        class="edit right_option"
        ref="mic_select"
        type="text"
        @change="changeMic"
      >
        <option
          v-for="item in mic_list"
          :key="item.index"
          :value="item.label"
          >{{ item.label }}</option
        >
      </select>
    </div>

    <div class="horizontal_line">
      <div class="left_option">Frame Size</div>
      <div style="flex: 0.1" class="divider">:</div>
      <select class="edit right_option" ref="select_size">
        <option value="default">default</option>
        <option value="640x360">640x360</option>
        <option value="1280x720">1280x720</option>
        <option value="1920x1080" selected>1920x1080</option>
      </select>
    </div>

    <div class="horizontal_line">
      <div class="left_option">Frame Rate</div>
      <div style="flex: 0.1" class="divider">:</div>
      <input class="edit right_option" type="text" v-model="frame_rate" />
    </div>

    <div class="horizontal_line">
      <v-btn
        id="publish_btn"
        style="flex: 1; margin-top: 20px"
        @click="recordVideo"
        >PUBLISH</v-btn
      >
    </div>

    <div style="width: 100%; height: 40px"></div>
  </div>
</template>

<script>
import * as CONFIG from "../utils/config";
import { mapMutations, mapState } from "vuex";
import {
  EventBus,
  EVENT_SET_AUDIO_LIST,
  EVENT_SET_CAMERA_ID,
  EVENT_SET_VIDEO_ID,
  EVENT_SET_VIDEO_LIST
} from "../utils/events-bus";
import { playVideo } from "../utils/webrtc/play";
import { publishVideo } from "../utils/webrtc/publish";

export default {
  data() {
    return {
      user_name: "",
      web_signaliing_url: "",
      receive_stream: "",
      sender_stream: "",
      video_list: [],
      mic_list: [],
      audio_bitrate: "",
      video_bitrate: "",
      frame_size_index: 0,
      frame_rate: ""
    };
  },
  computed: {
    ...mapState(["tab_contents_height"])
  },
  created() {
    this.$nextTick(() => {
      this.$refs.bg.style.height = this.tab_contents_height + "px";
      console.log(this.tab_contents_height);
    });

    EventBus.$on(EVENT_SET_VIDEO_LIST, videos => {
      console.log("RECEIVE", videos);
      this.video_list = videos;
    });

    EventBus.$on(EVENT_SET_AUDIO_LIST, audios => {
      this.mic_list = audios;
    });

    this.loadInfo();
  },
  methods: {
    ...mapMutations(["SET_NAME"]),
    applyName() {
      CONFIG.put(CONFIG.Key.STR_USER_NAME, this.user_name);
      this.SET_NAME(this.user_name);
    },
    loadInfo() {
      this.user_name = CONFIG.load(CONFIG.Key.STR_USER_NAME);
      this.web_signaliing_url = CONFIG.load(CONFIG.Key.STR_WEB_RTC_URL);
      this.receive_stream = CONFIG.load(CONFIG.Key.STR_WEB_RTC_RECEIVE_STREAM);
      this.sender_stream = CONFIG.load(CONFIG.Key.STR_WEB_RTC_SENDER_STREAM);
      this.audio_bitrate = CONFIG.load(CONFIG.Key.STR_WEB_RTC_AUDIO_BITRATE);
      this.video_bitrate = CONFIG.load(CONFIG.Key.STR_WEB_RTC_VIDEO_BITRATE);
      this.frame_size = CONFIG.load(CONFIG.Key.STR_WEB_RTC_FRAME_SIZE);
      this.frame_rate = CONFIG.load(CONFIG.Key.STR_WEB_RTC_FRMAE_RATE);
    },
    changeCam() {
      console.log("changeCam");
      console.log(this.$refs.cam_select.selectedIndex);
      const c = this.video_list[this.$refs.cam_select.selectedIndex];
      EventBus.$emit(EVENT_SET_CAMERA_ID, c.deviceId);
    },
    changeMic() {
      console.log("mic");
      console.log(this.$refs.mic_select.selectedIndex);
    },
    applyReceiverOption() {
      CONFIG.put(CONFIG.Key.STR_WEB_RTC_URL, this.web_signaliing_url);
      CONFIG.put(CONFIG.Key.STR_WEB_RTC_RECEIVE_STREAM, this.receive_stream);
    },
    applySenderOption() {
      CONFIG.put(CONFIG.Key.STR_WEB_RTC_URL, this.web_signaliing_url);
      CONFIG.put(CONFIG.Key.STR_WEB_RTC_SENDER_STREAM, this.sender_stream);
      CONFIG.put(CONFIG.Key.STR_WEB_RTC_VIDEO_BITRATE, this.video_bitrate);
      CONFIG.put(CONFIG.Key.STR_WEB_RTC_AUDIO_BITRATE, this.audio_bitrate);
      this.select_size = this.$refs.select_size.selectedIndex;
      const size = this.$refs.select_size.options[this.select_size].value;
      console.log(size);
      CONFIG.put(CONFIG.Key.STR_WEB_RTC_FRAME_SIZE, size);
      CONFIG.put(CONFIG.Key.STR_WEB_RTC_FRMAE_RATE, this.frame_rate);
    },
    showVideo() {
      EventBus.$emit(EVENT_SET_VIDEO_ID, 1);

      this.applyReceiverOption();
      playVideo();
    },
    recordVideo() {
      EventBus.$emit(EVENT_SET_VIDEO_ID, 0);

      this.applySenderOption();
      publishVideo();
    }
  }
};
</script>

<style scoped>
* {
  font-family: "Roboto";
}

.root {
  padding: 0px 20px 0px 20px;
  overflow-y: scroll;
}

.edit {
  width: 100%;
  height: 100%;

  outline: none !important;
  outline-width: 0 !important;
  box-shadow: none;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;

  border-bottom: 1px solid black;
}

.title {
  font-size: 20px;
  font-weight: bold;
  margin-top: 30px;
  margin-bottom: 5px;
}

.horizontal_line {
  height: 40px;
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.divider {
  text-align: center;
}

.right_input {
  flex: 6;
  margin-left: 20px;
}

.left_option {
  flex: 2;
}

.right_option {
  flex: 2;
  margin-left: 20px;
}

.right_button {
  text-align: right;
  margin-top: 10px;
}
</style>
